<template>
  <div class="create-post">
    <Panel>
      <template #header>
        <router-link to="/">主页</router-link> <span> / </span>
        <span>发布话题</span>
      </template>
      <template #content>
        <form action="">
          <div>
            <span>请选择板块: </span>
            <el-select v-model="topic.tab" placeholder="请选择">
              <el-option
                v-for="item in tab"
                :key="item.value"
                :label="item.label"
                :value="item.value"
              >
              </el-option>
            </el-select>
          </div>
          <div>
            <el-input
              placeholder="标题字数10字以上"
              minlength="10"
              class="title"
              v-model="topic.title"
              type="text"
            ></el-input>
          </div>
          <div>
            <!-- md编辑器 -->
            <!-- 用了md编辑器 向后端发送content 发送的是 md字符串，也就是写md语法，最后发md字符串-->
            <mavon-editor
              defaultOpen="edit"
              :subfield="false"
              :toolbars="toolbars"
              v-model="topic.content"
            />
          </div>
          <el-button @click="submit" type="primary">提交</el-button>
        </form>
      </template>
    </Panel>
  </div>
</template>

<script>
import Panel from "../containers/Panel.vue";
import axios from "../plugins/axios";
export default {
  data() {
    return {
      topic: {
        title: "",
        tab: "",
        content: "",
      },
      tab:[
          {
              value:'ask',
              label:'问答'
          },
          {
              value:'share',
              label:'分享'
          },
          {
              value:'job',
              label:'招聘'
          },
          {
              value:'dev',
              label:'客户端测试'
          },
      ],
      toolbars: {
        bold: true, // 粗体
        italic: true, // 斜体
        header: true, // 标题
        underline: true, // 下划线
        strikethrough: true, // 中划线
        mark: true, // 标记
        superscript: true, // 上角标
        subscript: true, // 下角标
        // quote: true, // 引用
        // ol: true, // 有序列表
        // ul: true, // 无序列表
        link: true, // 链接
        imagelink: true, // 图片链接
        code: true, // code
        table: true, // 表格
        fullscreen: true, // 全屏编辑
        readmodel: true, // 沉浸式阅读
        htmlcode: true, // 展示html源码
        help: true, // 帮助
        /* 1.3.5 */
        // undo: true, // 上一步
        // redo: true, // 下一步
        trash: true, // 清空
        save: true, // 保存（触发events中的save事件）
        /* 1.4.2 */
        navigation: true, // 导航目录
        /* 2.1.8 */
        alignleft: true, // 左对齐
        aligncenter: true, // 居中
        alignright: true, // 右对齐
        /* 2.2.1 */
        subfield: true, // 单双栏模式
        preview: true, // 预览
      },
    };
  },
  components: { Panel },
  methods: {
    async submit() {
      // 首先要判断输入的内容是否合理
      let { title, tab, content } = this.topic;
      const token = localStorage.getItem("token");
      const res = await axios.post("/topics", {
        accesstoken: token,
        tab,
        title,
        content,
      });
      // console.log(res);
      this.$router.push(`/topic/${res.topic_id}`);
    },
  },
};
</script>

<style lang='less'>
.create-post {
  input {
    outline: none;
  }
  .title {
    width: 100%;
    margin: 10px 0;
    // border: 0;
    // border: 1px solid #ccc;
    border-radius: 5px;
    line-height: 20px;
  }
}
</style>